import React from "react";
import { Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function Statistics({ transactions }) {
  const categories = ["food", "rent", "salary", "entertainment"];
  const data = {
    labels: categories,
    datasets: [
      {
        label: "Expenses",
        data: categories.map((category) =>
          transactions
            .filter((t) => t.category === category && t.type === "expense")
            .reduce((sum, t) => sum + t.amount, 0)
        ),
        backgroundColor: "rgba(255, 99, 132, 0.5)",
      },
      {
        label: "Income",
        data: categories.map((category) =>
          transactions
            .filter((t) => t.category === category && t.type === "income")
            .reduce((sum, t) => sum + t.amount, 0)
        ),
        backgroundColor: "rgba(54, 162, 235, 0.5)",
      },
    ],
  };

  return (
    <div>
      <h2>Statistics</h2>
      <Bar data={data} />
    </div>
  );
}

export default Statistics;
