import React, { useState, useEffect } from "react";
import "./App.css";
import TransactionForm from "./TransactionForm";
import TransactionList from "./TransactionList";
import Balance from "./Balance";
import Statistics from "./Statistics"; // Import der Statistics-Komponente
import Modal from "./Modal";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css"; // Importiere die Standard-Tab-Styles

function App() {
  const [transactions, setTransactions] = useState([]);
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    const savedTransactions = JSON.parse(localStorage.getItem("transactions"));
    console.log("Geladene Transaktionen aus Local Storage:", savedTransactions);
    if (savedTransactions && savedTransactions.length > 0) {
      setTransactions(savedTransactions);
    }
  }, []);

  useEffect(() => {
    console.log("Speichere Transaktionen in Local Storage:", transactions);
    localStorage.setItem("transactions", JSON.stringify(transactions));
  }, [transactions]);

  const addTransaction = (transaction) => {
    const newTransactions = [...transactions, transaction];
    setTransactions(newTransactions);
    console.log("Transaktion hinzugefügt:", newTransactions);
  };

  const deleteTransaction = (index) => {
    const newTransactions = transactions.filter((_, i) => i !== index);
    setTransactions(newTransactions);
    console.log("Transaktion gelöscht:", newTransactions);
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>Finanzverfolgungs-App</h1>
      </header>
      <Balance transactions={transactions} />{" "}
      {/* Balance ganz oben platzieren */}
      <Tabs>
        <TabList>
          <Tab>Übersicht</Tab>
          <Tab>Statistik</Tab>
        </TabList>

        <TabPanel>
          <div className="form-button-container">
            <button className="form-button" onClick={() => setShowModal(true)}>
              Eintrag hinzufügen
            </button>
          </div>
          {showModal && (
            <Modal onClose={() => setShowModal(false)}>
              <TransactionForm addTransaction={addTransaction} />
            </Modal>
          )}
          <TransactionList
            transactions={transactions}
            deleteTransaction={deleteTransaction}
          />
        </TabPanel>
        <TabPanel>
          <Statistics transactions={transactions} />{" "}
          {/* Statistik in eigenes Fenster */}
        </TabPanel>
      </Tabs>
    </div>
  );
}

export default App;
